// æ£€æŸ¥æ—¥æœŸæ˜¯å¦æœ‰æ•ˆ
export const isValidDate = (dateStr: string): boolean => {
  try {
    const [year, month, day] = dateStr.split('-').map(Number)
    const date = new Date(year, month - 1, day)
    return (
      date.getFullYear() === year &&
      date.getMonth() === month - 1 &&
      date.getDate() === day &&
      year >= 1900 &&
      year <= 2100
    )
  } catch {
    return false
  }
}

